import jsonp from 'common/js/jsonp'
import {
  commonParams,
  option
} from './config'
// import axios from 'axios'

export function getRecommend() {
  const url = 'https://c.y.qq.com/v8/fcg-bin/fcg_first_yqq.fcg?format=jsonp&tpl=v12&page=other'
  // body...
  const data = Object.assign({}, commonParams, {
    platform: 'yqq',
    loginUin: 0,
    needNewCode: 0
  })

  return jsonp(url, data, option)
}
export function getDiscList() {
  const url = 'https://c.y.qq.com/v8/fcg-bin/fcg_first_yqq.fcg?format=jsonp&tpl=v12&page=other'

  const data = Object.assign({}, commonParams, {
    platform: 'yqq',
    loginUin: 0,
    hostUin: 0,
    needNewCode: 0
  })

  return jsonp(url, data, option)
}
export function getSongList(disstid) {
  const url = 'https://c.y.qq.com/qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg'

  const data = Object.assign({}, commonParams, {
    disstid,
    g_tk: 5381,
    type: 1,
    json: 1,
    utf8: 1,
    onlysong: 0,
    platform: 'h5',
    hostUin: 0,
    needNewCode: 0,
    nosign: 1
  })

  return jsonp(url, data, option)
}



